import React, { Component } from 'react';
import './Audio.scss';

 class Audio extends Component {
    constructor(props){
        super(props);
    }

    render() {
        let {pathUrl} = this.props;
        return (
            <div className={'audio'}>
                <div class="jplayer" id="myid" data-jptitle="Charlie Puth - Attention"
                 data-jpsrc="https://audio-ssl.itunes.apple.com/apple-assets-us-std-000001/AudioPreview122/v4/b1/eb/c1/b1ebc105-bb9b-53d8-aca6-d19497b89a54/mzaf_594361230071145654.plus.aac.p.m4a"  
                 poster="https://img.youtube.com/vi/9vQ6X_xFSYw/hqdefault.jpg">
                </div>

                <audio controls poster="https://img.youtube.com/vi/9vQ6X_xFSYw/hqdefault.jpg">
                <source src="https://github.com/bbmDiscipleTracker/musicPodcast/blob/master/Harvest%202019%20Session%201%20Apostle%20NN%20Mcoteli.mp3" type="audio/mp3"/>
                </audio>
            </div>
        )
    }
}

export default Audio;
