import React, { Component } from 'react'
import './Sermons.scss';
import AudioComponent from './audio/Audio';
import VideoComponent from './video/Video';

class Sermons extends Component {
  constructor(props){
    super(props);

    this.state = ({
      isAudioPodcast: true
    })
}

togglePodcast(){
  let {isAudioPodcast} = this.state;
  this.setState({
      isAudioPodcast : !isAudioPodcast
  })
}


  render() {
    let {isAudioPodcast} = this.state;
    let podcastType = isAudioPodcast? 'Video Podcast' : 'Audio Podcast';
    return (
      <div className={'sermon'}>
        <button className={'notton-podcast'} onClick={()=>this.togglePodcast}>{podcastType}</button>
        {isAudioPodcast&& <AudioComponent/>}
        {!isAudioPodcast&& <VideoComponent/>}
      </div>
    )
  }
}


export default Sermons;
